import { connect } from 'react-redux';
import LangSwitcher from '../../components/player/LangSwitcher';
import {stopRecording} from '../../actions/stream';
import { Servers, changeServer } from '../../actions/player';

const mapStateToProps = state => {
  return {
    servers: Servers,
    server: state.transcript.server,
  };
}

const mapDispatchToProps = dispatch => {
  return {
    onServerChange: (event, index, server) => {
      dispatch(changeServer(server));
      dispatch(stopRecording());
    },
    initServer: server => dispatch(changeServer(server)),
  };
}

const LangSwitcherContainer = connect(
  mapStateToProps,
  mapDispatchToProps,
)(LangSwitcher);

export default LangSwitcherContainer;



