import './index.css';
import React from 'react';
import Drawer from 'material-ui/Drawer';
// import Controls from './Controls';
import Player from "../../containers/player";
// import StreamPane from "../../containers/player/StreamPane";
import LangSwitcher from "../../containers/player/LangSwitcher";
//import SpeechRate from "../../containers/transcript/SpeechRate";
import Logo from '../ui/Logo';
import Footer from "../ui/Footer";

const Sidebar = (props) => {
  // We use just the microphone input for now but I don't
  // want to remove all the code for the upload yet..
  // So we use here only the StreamPane component instead
  // of the Player component that would show the two tabs.
  // The Controls component is also unused for now.
  return(
    <Drawer
      className="Sidebar"
      containerClassName="Sidebar__drawer"
      overlayClassName="Sidebar__overlay"
      open={true}>
      <header className="Sidebar__header">
        <Logo />
        <LangSwitcher />
      </header>
      <div className="Sidebar__body">
        <Player />
        {/*<StreamPane />*/}
        {/* <SpeechRate /> */}
      </div>
      <footer className="Sidebar__footer">
        <Footer />
      </footer>
    </Drawer>
  );
};



export default Sidebar;



