import { checkCompliance } from './compliance';
import identity from '../modules/identity';

export const clearTranscript = () => {
  return {
    type: 'CLEAR_TRANSCRIPT',
  };
};

export const speechStart = timestamp => {
  return {
    type: 'SPEECH_START',
    timestamp,
  };
};

export const speechFrame = data => {
  return dispatch => {
    const text = data.result.hypotheses[0].transcript;
    if (data.speakerId === identity)
      dispatch(checkCompliance(text));
    dispatch({
      type: 'SPEECH_FRAME',
      segment: data,
    });
  };
};

export const speechStop = () => {
  return {
    type: 'SPEECH_STOP',
  };
};

export const addSpeaker = (id, peerConnection) => {
  return {
    type: 'ADD_SPEAKER',
    id,
    peerConnection,
  };
};

export const transcriptionDone = data => {
  return dispatch => {
    dispatch(speechFrame(data));
    dispatch({
      type: 'TRANSCRIPTION_DONE',
      data,
    });
  }
};



