import './index.css';
import React from 'react';
import PropTypes from 'prop-types';
// import IconButton from 'material-ui/IconButton';
// import FontIcon from 'material-ui/FontIcon';
import Segment from './Segment';

class Transcript extends React.Component {
  componentWillMount = () => {
    this.props.generateId();
  }
        // <Subheader className="Transcript__header">
        //   <IconButton
        //     className="Transcript__clear"
        //     tooltip="Clear"
        //     onClick={props.onTranscriptClear}>
        //     <FontIcon className="material-icons">
        //       clear
        //     </FontIcon>
        //   </IconButton>
        // </Subheader>

  render = () => {
    const props = this.props;
    return(
      <div className="Transcript">
        <div className="Transcript__body">
          <ul className="Transcript__list">
            {props.segments.map((segment, idx) => <Segment segment={segment} key={idx}/>)}
            <li style={{ float:"left", clear: "both" }}
              ref={(el) => { this.transcriptEnd = el; }} />
          </ul>
        </div>
      </div>
    );
  }

  componentDidUpdate = () => this.transcriptEnd.scrollIntoView({ behavior: "smooth" });
}

Transcript.propTypes = {
  onTranscriptClear: PropTypes.func.isRequired,
  text: PropTypes.string,
  segments: PropTypes.arrayOf(PropTypes.shape({ msg: PropTypes.string })),
};

export default Transcript;



