import './Rightbar.css';
import React from 'react';
import Subheader from 'material-ui/Subheader';
import Paper from 'material-ui/Paper';
import Chip from 'material-ui/Chip';
import { translate } from 'react-i18next';

class Rightbar extends React.Component {
  render = () => {
    const compliantClassName = this.props.compliant ? 'compliant':'noncompliant';
    return(
      <div className="Rightbar__container">
        <Paper zDepth={2} className="Rightbar__card">
          <Subheader className="Rightbar__header">
            <h3 className="Rightbar__title">{this.props.title}</h3>
            <p className="Rightbar__desc">{this.props.desc}</p>
          </Subheader>
          <Chip className={"Rightbar__tag "+compliantClassName}>
            {this.props.t(this.props.compliant ? "compliant_tag" : "noncompliant_tag")}
          </Chip>
          <div className="Rightbar__body">
            {this.props.children}
          </div>
        </Paper>
      </div>
    );
  }
}

export default translate('translations')(Rightbar);



