import { createSelector } from 'reselect'

export const getSpeechStartTime = state => state.transcript.speechStartTime
export const getTranscript = state => state.transcript.segments
export const getSegments = createSelector(
  getTranscript,
  segments => {
    console.log('lol')
    console.log(segments)
    const seglist = Object.keys(segments).map(key => segments[key])
    const sorted = seglist.slice().sort((a, b) => {
      if (a.timestamp < b.timestamp) return -1
      if (a.timestamp > b.timestamp) return 1
      return 0
    })
    return sorted
  }
)

export const getLines = createSelector(
  getSegments,
  segments => segments.map(segment => {
    if (segment.speaker === 'spk1') return segment.finalText
    else return ''
  })
)

export const getSpeechRate = createSelector(
  [getLines, getSpeechStartTime],
  (lines, startTime) => {
    const elasped = new Date() - startTime
    const words = lines.map(line => line.split(' ').length).reduce((a, b) => a + b, 0)
    return Math.floor(words / (elasped / 1000 / 60))
  }
)



