const initialState = {
  rules: [],
};

const complianceReducer = (state = initialState, action) => {
  switch(action.type) {
    case 'COMPLIANCE_RULES_LOADED':
      return {...state, rules: action.rules};
    case 'COMPLIANCE_CHANGE':
      return {...state, rules: updateCompliance(state.rules.slice(), action.ids)};
    default:
      return state;
  }
};

const updateCompliance = (rules, ids) => rules.map((rule) => {
  if (ids.includes(rule._id))
    rule.checked = true;
  return rule;
});

export default complianceReducer;



