const initialState = {
  open: true,
  msg: '',
}

const notificationsReducer = (state = initialState, action) => {
  switch(action.type) {
    case 'CLOSE_NOTIFICATION':
      return {...state, open: false}; 
    case 'SHOW_NOTIFICATION':
      return {...state, open: true, msg: action.msg};
    default:
      return state;
  }
}

export default notificationsReducer;



