import './Log.css';
import React from 'react';

const logLine = (line, idx) => <span className="Log__line" key={idx}>{`>> ${line.lvl}: ${line.text}`}<br/></span>;

class Log extends React.Component {
  render = () => {
    const props = this.props;
    return(
      <div className="Log">
        {props.logs.map(logLine)}
        <div style={{ float:"left", clear: "both" }}
          ref={(el) => { this.transcriptEnd = el; }} />
      </div>
    );
  }

  componentDidUpdate = () => this.transcriptEnd.scrollIntoView({ behavior: "smooth" });
}

export default Log;



