/* global WaveSurfer */

import { dispatch } from '../store';
import { playerReady, stop } from '../actions/upload';
import { speechStop } from '../actions/transcript';
import transcriper from './transcriper';

const BUFFER_SIZE = 2048;

class Player {
  constructor(containerId) {
    this.wavesurfer = WaveSurfer.create({
      container: `#${containerId}`,
      height: 50,
    });

    this.wavesurfer.on('ready', this._onready);
    this.wavesurfer.on('finish', this._onfinish);
  }

  _onaudioprocess = (e) => {
    const inputBuffer = e.inputBuffer;
    const outputBuffer = e.outputBuffer;
    const inputData = inputBuffer.getChannelData(0);
    const outputData = outputBuffer.getChannelData(0);

    for (let sample = 0; sample < inputBuffer.length; sample++) {
      outputData[sample] = inputData[sample];
    }

    if (transcriper.isOpen())
      transcriper.transcripe(inputData);
  }

  _onready = () => {
    const recorder = this.wavesurfer.backend.ac.createScriptProcessor(BUFFER_SIZE, 1, 1);
    recorder.onaudioprocess = this._onaudioprocess;
    this.wavesurfer.backend.setFilter(recorder);
    dispatch(playerReady());
  }

  _onfinish = () => {
    dispatch(stop());
    dispatch(speechStop());
  }

  load = (file) => this.wavesurfer.loadBlob(file);

  play = () => this.wavesurfer.play();

  pause = () => this.wavesurfer.pause();

  stop = () => this.wavesurfer.stop();

  togglePlaying = () => this.wavesurfer.playPause();

  isPlaying = () => this.wavesurfer && this.wavesurfer.isPlaying();

  isPaused = () => this.wavesurfer && this.wavesurfer.getCurrentTime() > 0;

  destroy = () => this.wavesurfer.destroy();
}

export default Player;



