import './LangSwitcher.css';
import React from 'react';
import {Servers} from '../../actions/player';
import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
import { translate } from 'react-i18next';

class LangSwitcher extends React.Component{
  componentWillMount = () => {
    this.props.initServer(this.props.server);
  }

  render = () => {
    const props = this.props;
    return(
      <div className="LangSwitcher">
          <SelectField
            className="LangSwitcher__selectField"
            onChange={props.onServerChange}
            floatingLabelText={props.t("language_label")}
            value={props.server}>
            { Object.keys(props.servers).map(this.renderServer) }
          </SelectField>
      </div>
    );
  }

  renderServer = (server) => <MenuItem value={server} key={server} primaryText={Servers[server].label} />;
}

export default translate('translations')(LangSwitcher);



