
import axios from 'axios';
import { dispatch }  from '../store';
import openSocket from 'socket.io-client';
import identity from './identity';
import { speechFrame, addSpeaker } from '../actions/transcript';


const port = 443;
const host = 'speechdemo.contextaservices.com';
const baseURL = `https://${host}:${port}/`;
const socketURL = `https://${host}:444/`;

const server = axios.create({
  baseURL
});

const backendSocket = openSocket(socketURL, { secure: false });

backendSocket.on('connect', () => {
  dispatch(addSpeaker(identity));
  backendSocket.emit('NEW_SPEAKER', identity);
});

backendSocket.on('SPEAKER', id => {
  dispatch(addSpeaker(id));
  backendSocket.emit('OLD_SPEAKER', identity);
});

backendSocket.on('OLD_SPEAKER', id => {
  dispatch(addSpeaker(id));
});

backendSocket.on('SPEECH_FRAME', data => {
  dispatch(speechFrame(data));
});

export const broadcastSpeechFrame = data => {
  backendSocket.emit('SPEECH_FRAME', identity, data);
}

export const get = (path) => server.get(path).then((response) => {
  return response.data;
});




