import './Logo.css';
import React from 'react';
import logo from '../../images/logo-logotype.png';

class Logo extends React.Component {
  render = () => {
    return(
      <div className="Logo">
        <img src={logo} className="Logo__img" alt="Contexta"/>
      </div>
    );
  }
}


export default Logo;



