import identity from './identity'

const upcaseFirstLetter = str => {
  return str.charAt(0).toUpperCase() + str.slice(1)
}

const replaceUnks = str => {
  return str.split('<unk>').join('***')
}

const makeSegment = (speakers, seg) => {
  const speaker = speakers[seg.speakerId]
  const segment = {
    id: seg.id + '_' + seg.segment,
    segnum: seg.segment,
    text: upcaseFirstLetter(replaceUnks(seg.result.hypotheses[0].transcript)),
    speaker: speaker.name,
    color: speaker.color,
    timestamp: seg.timestamp,
    final: seg.result.final
  }
  return segment
}

const makefinal = segment => {
  segment.finalText = segment.text
  segment.text = ''
  return segment
}

const mergeSegments = (transcript, lastsegment, newsegment) => {
  if (lastsegment.finalText.length + newsegment.text.length > 200) {
    lastsegment.text = ''
    if (newsegment.final) newsegment = makefinal(newsegment)
    return [...transcript, newsegment]
  }

  if (newsegment.final) {
    lastsegment.finalText += ' ' + newsegment.text
    lastsegment.text = ''
  } else {
    lastsegment.text = newsegment.text
  }
  return transcript
}

export const updateTranscript = (transcript, speakers, data) => {
  const newtranscript = {...transcript}
  const newsegment = makeSegment(speakers, data)

  if (newsegment.text === '***.') return transcript

  if (!newtranscript[newsegment.id] || !newtranscript[newsegment.id].final) {
    newtranscript[newsegment.id] = newsegment
  }
  return newtranscript
  // if (newsegment.text === '***.') return transcript

  // if (!transcript.length) {
  //  if (newsegment.final) makefinal(newsegment)
  //  return [...transcript, newsegment]
  // }

  // const lastsegment = transcript[transcript.length - 1]
  // if (lastsegment.speaker !== newsegment.speaker) {
  //  if (newsegment.final) makefinal(newsegment)
  //  return [...transcript, newsegment]
  // }

  // return mergeSegments(transcript, lastsegment, newsegment)
}

export const calcSpeechRate = (segment, speechRate) => {
  if (segment.speakerId !== identity) {
    return speechRate
  }

  // minutes
  const length = parseFloat(segment['segment-length']) / 60

  let text = segment.result.hypotheses[0].transcript
  text = text.split('<unk>').join('')
  text = text.split('mhm').join('')
  text = text.split('.').join('')
  text = text.trim()
  if (!text.length) {
    return 0
  }

  const numWords = text.split(' ').length
  return Math.floor(numWords / length)
}



