export const uploadFile = file => {
  return {
    type: 'FILE_UPLOADED',
    file,
  };
}

export const playerReady = () => {
  return {
    type: 'PLAYER_READY'
  };
}

export const play = () => {
  return {
    type: 'PLAY',
  };
}

export const pause = () => {
  return {
    type: 'PAUSE',
  };
}

export const stop = () => {
  return {
    type: 'STOP',
  };
}



