import i18n from '../i18n'

export const PlayerModes = {
  STREAM: 0,
  UPLOAD: 1,
};

export const Servers = {
  NEDERLANDS: {
    url: 'wss://speechdemo.contextaservices.com:8888/client/ws/',
    label: 'Nederlands',
    index: 0,
    lang: 'nl',
  },
  ENGLISH: {
    url: 'wss://speechdemo.contextaservices.com:8844/client/ws/',
    label: 'English',
    index: 1,
    lang: 'en',
  },
};

export const changeServer = server => {
  i18n.changeLanguage(Servers[server].lang);
  return {
    type: 'SERVER_CHANGE',
    url: Servers[server].url,
    server,
  };
};

export const changeMode = mode => {
  return {
    type: 'MODE_CHANGE',
    mode,
  };
};

export const log = (lvl, text) => {
  return {
    type: 'ADD_LOG',
    lvl,
    text
  };
};



