import './UploadPane.css';
import React from 'react';
import DropZone from 'react-dropzone';
import FloatingActionButton from 'material-ui/FloatingActionButton';
import FontIcon from 'material-ui/FontIcon';

const playIcon = <FontIcon className="material-icons">play_arrow</FontIcon>;
const pauseIcon = <FontIcon className="material-icons">pause</FontIcon>;
const stopIcon = <FontIcon className="material-icons">stop</FontIcon>;

const filename = (name) => {
  if (name.length > 15) {
    return name.substring(0, 15) + '...';
  } else {
    return name;
  }
}

const fileInfo = (file) => {
  if (file) {
    return <div className="UploadPane__fileInfo">{`${filename(file.name)} (${file.size})`}</div>;
  } else {
    return <div className="UploadPane__fileInfo" />;
  }
}

const UploadPane = (props) => {
  let primaryIcon = props.playing ? pauseIcon : playIcon;

  return(
    <div className="UploadPane__container" >

      <DropZone
        className="UploadPane__dropzone"
        multiple={false}
        accept="audio/*"
        onDrop={props.onDrop}>
        <h4 className="UploadPane__title">Select or drop a file</h4>
      </DropZone>

      {fileInfo(props.file)}

      <div className="UploadPane__playerContainer">
        <FloatingActionButton
          className="UploadPane__button"
          disabled={!props.ready}
          onClick={() => props.primaryClick(props.playing)}>
          {primaryIcon}
        </FloatingActionButton>
        <FloatingActionButton
          className="UploadPane__button"
          onClick={props.stopClick}
          disabled={!props.playing && !props.paused}>
          {stopIcon}
        </FloatingActionButton>
      </div>

    </div>
  );
}

export default UploadPane;



