import { dispatch } from '../store';
import { log } from '../actions/player';
import transcriper from './transcriper';

const BUFFER_SIZE = 2048;

class Streamer {
  open = () => {
    navigator.mediaDevices.getUserMedia({audio: true, video: false})
      .then((stream) => {
        this.initRecorder(stream);
      })
      .catch((err) => {
        dispatch(log('ERR','Could not get access to microphone'));
      });
  }

  close = () => {
    if (this.context)
      this.context.close();
  }

  initRecorder = (stream) => {
    this.context = new window.AudioContext();
    const audioInput = this.context.createMediaStreamSource(stream);
    const recorder = this.context.createScriptProcessor(BUFFER_SIZE, 1, 1);
    recorder.onaudioprocess = this._onaudioprocess;
    audioInput.connect(recorder);
    recorder.connect(this.context.destination);
  }

  _onaudioprocess = (e) => {
    const inputBuffer = e.inputBuffer;
    const inputData = inputBuffer.getChannelData(0);
    if (transcriper.isOpen())
      transcriper.transcripe(inputData);
  }
}

export default new Streamer();



