import { get } from '../modules/backendService';
import { log } from './player';

export const checkCompliance = text => {
  return dispatch => get(`/check?segment="${text}"`).then((response) => {
    const complianceRuleIds = response.data;
    dispatch(complianceChange(complianceRuleIds));
  }).catch((err) => {
    dispatch(log('ERR', err.message));
  });
};

export const loadComplianceRules = () => {
  return dispatch => get('/list').then((response) => {
    const rules = response.data;
    dispatch(rulesLoaded(rules))
  }).catch((err) => {
    dispatch(log('ERR', err.message));
  });
};

const rulesLoaded = rules => {
  return {
    type: 'COMPLIANCE_RULES_LOADED',
    rules,
  };
};

const complianceChange = (complianceRuleIds) => {
  return {
    type: 'COMPLIANCE_CHANGE',
    ids: complianceRuleIds,
  };
};



