import { connect } from 'react-redux';
import CompliancePane from '../../components/usecases/compliance';
import { loadComplianceRules } from '../../actions/compliance';
import { getIsCompliant } from '../../selectors/compliance';

const mapStateToProps = state => {
  const props = {...state.compliance};
  props.compliant = getIsCompliant(state);
  return props;
}

const mapDispatchToProps = dispatch => {
  return {
    loadComplianceRules: () => dispatch(loadComplianceRules()),
  }
};

const ComplianceContainer = connect(
  mapStateToProps,
  mapDispatchToProps,
)(CompliancePane);

export default ComplianceContainer;



