import React from 'react';
import Snackbar from 'material-ui/Snackbar';

const Notification = (props) => {
  return <Snackbar
            open={props.open}
            message={props.msg}
            autoHideDuration={4000}
            onRequestClose={props.closeNotification} />;
}

export default Notification;



