import { connect } from 'react-redux';
import StreamPane from '../../components/player/StreamPane';
import {stopRecording, startRecording} from '../../actions/stream';
import {speechStart, speechStop} from '../../actions/transcript';

const toggleRecording = (dispatch, recording) => {
  if (recording) {
    dispatch(stopRecording());
    dispatch(speechStop());
  } else {
    dispatch(startRecording());
    dispatch(speechStart(new Date()));
  }
}

const mapStateToProps = state => {
  return state.player.streamPane
};

const mapDispatchToProps = dispatch => {
  return{
    toggleRecording: recording => toggleRecording(dispatch, recording)
  };
};

const StreamPaneContainer = connect(
  mapStateToProps,
  mapDispatchToProps,
)(StreamPane);

export default StreamPaneContainer;



