import './StreamPane.css';
import React from 'react';
import FontIcon from 'material-ui/FontIcon';
import FlatButton from 'material-ui/FlatButton';
import { translate } from 'react-i18next';

const playIcon = <FontIcon className="material-icons">mic</FontIcon>;
const cancelIcon = <FontIcon className="material-icons">pause</FontIcon>;

const StreamPane = (props) => {
  let className = props.recording ? 'is-streaming' : '';
  let label = props.recording ? 'stop_streamer' : 'start_streamer';
  // <h4 className="StreamPane__title">Press the button to start streaming your microphone</h4>
  return(
    <div className="StreamPane">
      <FlatButton
        className={'StreamPane__button ' + className}
        label={props.t(label)}
        labelPosition="after"
        onClick={() => props.toggleRecording(props.recording)}
        secondary={!props.recording}
        icon={!props.recording ? playIcon : cancelIcon} />
    </div>
  );
};

export default translate('translations')(StreamPane);



