import { combineReducers } from 'redux';
import { PlayerModes } from '../actions/player';
import uploadReducer from './upload'
import streamReducer from './stream';

export const mode = (state = PlayerModes.STREAM, action) => {
  switch(action.type) {
    case 'MODE_CHANGE':
      return action.mode;
    default:
      return state;
  }
};

export const playerReducer = combineReducers({ 
  uploadPane: uploadReducer,
  streamPane: streamReducer,
});



