import { connect } from 'react-redux';
import UploadPane from '../../components/player/UploadPane';
import { uploadFile, pause, play, stop } from '../../actions/upload';
import { speechStart, speechStop } from '../../actions/transcript';

const primaryClick = (playing, dispatch) => {
  if (playing) {
    dispatch(pause());
    dispatch(speechStop());
  } else {
    dispatch(play());
    dispatch(speechStart(new Date()));
  }
}

const mapStateToProps = state => {
  return state.player.uploadPane;
};

const mapDispatchToProps = (dispatch) => {
  return{
    onDrop: files => {
      dispatch(uploadFile(files[0]));
      dispatch(speechStop());
    },
    primaryClick: playing => primaryClick(playing, dispatch),
    stopClick: () => {
      dispatch(stop());
      dispatch(speechStop());
    },
  };
};

const UploadPaneContainer = connect(
  mapStateToProps,
  mapDispatchToProps,
)(UploadPane);

export default UploadPaneContainer;



