import './Segment.css'
import React from 'react'

const Segment = props => {
  if (props.segment.finalText || props.segment.text) {
    const side = props.segment.speaker === 'Speaker 1' ? 'left' : 'right'

    // `hoverColor={props.segment.color}`
    // The above prop has been removed. I am not sure though if we
    // need to keep it in order to change accordingly to the
    // response from the server. If not we can get rid of it.
    return <li className='Segment'>
      <h4>{props.segment.speaker}</h4>
      <p className={'triangle-border ' + side}>{props.segment.text}</p>
    </li>
  } else {
    return <span />
  }
}

export default Segment



