import './compliance.css';
import React from 'react';
import Rightbar from '../ui/Rightbar';
import {List, ListItem} from 'material-ui/List';
import Checkbox from 'material-ui/Checkbox';
import { translate } from 'react-i18next';
import i18n from '../../i18n'

class CompliancePane extends React.Component {
  componentWillMount = () => {
    this.props.loadComplianceRules();
  }

  render = () => {
    const props = this.props;
    return(
      <Rightbar 
        title={props.t("compliance_title")}
        desc={props.t("compliance_description")}
        compliant={props.compliant} >
        <List className="Compliance__list">
          {props.rules.map(this.renderCondition)}
        </List>
      </Rightbar>
    );
  }

  renderCondition = (condition, idx) => {
    return(
      <ListItem className="Compliance__listItem" key={idx}>
        <Checkbox
          className="Compliance__checkbox"
          label={`${idx+1}. ${condition.text[i18n.language]}`}
          checked={condition.checked}/>
      </ListItem>
    );
  }
}

export default translate('translations')(CompliancePane);



