import { log } from '../actions/player';
import { dispatch } from '../store';
import { transcriptionDone } from '../actions/transcript';
import identity from './identity';
import { showNotification } from '../actions/notifications';

// add content-type query string to url
const prepare = (url) => {
  url += '?content-type=audio/x-raw,+layout=(string)interleaved,+rate=(int)44100,+format=(string)F32LE,+channels=(int)1';
	return url;
}

class Transcriper {
  changeServer = (url) => {
    this.close();
    this.speechUrl = url + 'speech';

    if (this.statusSocket) this.statusSocket.close();
    const statusUrl = url + 'status';
    this.statusSocket = new WebSocket(statusUrl);
    this.statusSocket.onmessage = this.statusMessage;
    this.statusSocket.onerror = (e) => console.log(e);
    this.statusSocket.onerror = () => dispatch(showNotification(`Server could not be reached (Is it running?).`))
  }

  open = () => {
    this.close();
    this.socket = new WebSocket(prepare(this.speechUrl));
    this.socket.onmessage = this.message;
    this.socket.onopen = () => dispatch(log('INFO', `connected successfully to ${this.speechUrl}`));
  }

  close = () => {
    if (this.socket && this.socket.readyState === 1) {
      this.socket.send('EOS');
      this.socket.close();
    }
  }

  message = (e) => {
    const data = JSON.parse(e.data);
    data.timestamp = new Date();
    data.speakerId = identity;
    dispatch(transcriptionDone(data));
  }

  statusMessage = (e) => {
    const data = JSON.parse(e.data);
    console.log(data);
    dispatch(showNotification(`${data.num_workers_available} worker(s) available`));
  }

  transcripe = (buffer) => {
    if (this.socket.readyState === 1) {
      this.socket.send(buffer);
    } 
  }

  isOpen = () => this.socket && this.socket.readyState === 1;
}

export default new Transcriper();



