import Player from '../modules/player'

const initialUploadState = {
  file: null,
  ready: false,
  playing: false,
  paused: false,
  player: null,
};

const uploadReducer = (state = initialUploadState, action) => {
  switch(action.type) {
    case 'FILE_UPLOADED':
      const player = state.player ? state.player : new Player('waveform');
      player.load(action.file);
      return {...state, playing: false, ready: false, file: action.file, player};
    case 'PLAYER_READY':
      return {...state, ready: true };
    case 'PLAY':
      state.player.play();
      return {...state, playing: true, paused: false};
    case 'PAUSE':
      state.player.pause();
      return {...state, playing: false, paused: true};
    case 'STOP':
      state.player.stop();
      return {...state, playing: false, paused: false};
    default:
      return state;
  }
}

export default uploadReducer;



