import './index.css';
import React from 'react';
import {PlayerModes} from '../../actions/player';
import FontIcon from 'material-ui/FontIcon';
import {BottomNavigation, BottomNavigationItem} from 'material-ui/BottomNavigation';
import StreamPane from '../../containers/player/StreamPane';
import UploadPane from '../../containers/player/UploadPane';
import Log from '../../containers/player/Log'; // @@disabledfornow

const uploadIcon = <FontIcon className="material-icons">cloud_upload</FontIcon>;
const micIcon = <FontIcon className="material-icons">mic</FontIcon>;

class Player extends React.Component{

  render = () => {
    const props = this.props;
    return(
      <div className="Player">
        <BottomNavigation selectedIndex={props.mode}>
          <BottomNavigationItem
             label="Microphone"
             icon={micIcon}
             onClick={() => props.onModeChange(PlayerModes.STREAM)} />
           <BottomNavigationItem
             label="Upload File"
             icon={uploadIcon}
             onClick={() => props.onModeChange(PlayerModes.UPLOAD)} />
        </BottomNavigation>
        { props.mode === PlayerModes.STREAM ? <StreamPane /> : <UploadPane /> }
        <Log />
      </div>
    );
  }
}

export default Player;



