import './Footer.css';
import React from 'react';

const today = new Date();
const year = today.getFullYear();

class Footer extends React.Component {

  render = () => {
    return(
      <div className="Footer">
        <p>&copy; {year} Contexta Solutions</p>
      </div>
    );
  }
}

export default Footer;



